export interface FactoryCase {
  id: string;
  title: string;
  status: string;
  created_at: string;
  [key: string]: any;
}

export const factoryApi = {
  /**
   * æ¡ˆä»¶ä¸€è¦§ã‚’å–å¾—
   */
  getCases: async (status?: string): Promise<FactoryCase[]> => {
    try {
      const params = status ? { status } : {};
      const response = await apiClient.get<{ success: boolean; cases: FactoryCase[] }>('/admin/api/cases', { params });
      
      if (response.data.success && response.data.cases) {
        return response.data.cases;
      }
      
      // äº’æ›æ€§ã®ãŸã‚ã€casesãŒç›´æ¥é…åˆ—ã®å ´åˆã‚‚å¯¾å¿œ
      if (Array.isArray(response.data)) {
        return response.data;
      }
      
      return [];
    } catch (error: any) {
      console.error('æ¡ˆä»¶å–å¾—APIã‚¨ãƒ©ãƒ¼:', error);
      if (error.response) {
        console.error('ãƒ¬ã‚¹ãƒãƒ³ã‚¹:', error.response.data);
        console.error('ã‚¹ãƒ†ãƒ¼ã‚¿ã‚¹:', error.response.status);
      }
      throw error;
    }
  },

  /**
   * æ¡ˆä»¶ã®ã‚¹ãƒ†ãƒ¼ã‚¿ã‚¹ã‚’æ›´æ–°
   */
  updateCaseStatus: async (pageId: string, status: string): Promise<void> => {
    await apiClient.post('/admin/api/update-status', {
      page_id: pageId,
      status,
    });
  },

  /**
   * ã‚³ãƒ¡ãƒ³ãƒˆã‚’è¿½åŠ 
   */
  addComment: async (pageId: string, comment: string): Promise<void> => {
    await apiClient.post('/admin/api/add-comment', {
      page_id: pageId,
      comment,
    });
  },
};

